<template>
  <v-container
    fluid
    grid-list-md>
    <v-layout
      row
      wrap>
      <v-flex md12>
        <material-card
          color="primary"
          title="Lan√ßamento Extra">
          <v-card class="elevation-0">
            <v-card-text>
              <v-form>
                <v-layout
                  row
                  wrap>
                  <v-flex md12>
                    <core-filtro-rede-empresa-loja
                      v-model="filtros"
                      :on-change-rede="onChangeRede"
                      :on-change-empresa="onChangeEmpresa"
                      :loja-selecionada="lojaSelecionada"
                    />
                  </v-flex>
                </v-layout>
              </v-form>
            </v-card-text>
          </v-card>
        </material-card>
      </v-flex>
      <v-flex md12>
        <v-card
          v-if="filtros.lojaId"
        >
          <v-toolbar
            flat
            color="white">
            <v-toolbar-title>Lan√ßamentos de Repasse Digitados (N√£o Processados)</v-toolbar-title>
            <v-divider
              class="mx-2"
              inset
              vertical />
            <v-spacer />
            <v-btn
              small
              fab
              color="primary">
              <v-icon @click="openDialogNew()">mdi-plus</v-icon>
            </v-btn>
          </v-toolbar>
          <v-progress-linear
            :active="loading"
            color="light-blue"
            indeterminate
            height="3"
          />
          <v-data-table
            :headers="headers"
            :items="items"
            class="elevation-1 arrow-pagintion"
            hide-actions
          >
            <template
              slot="items"
              slot-scope="{ item }"
              ma-5>
              <td>{{ item.nomeLoja }}</td>
              <td>{{ item.tipoLancamento }}</td>
              <td>{{ item.data | moment("DD/MM/YYYY") }}</td>
              <td>{{ item.valor.toLocaleString("pt-BR", { minimumFractionDigits: 2 }) }}</td>
              <td>{{ item.historico }}</td>
              <td class="justify-end layout mr-2">
                <v-icon
                  v-if="item.temAnexo"
                  class="mr-2"
                  title="Baixar Anexo"
                  @click="downloadAnexo(item)"
                >mdi-cloud-download-outline</v-icon>
                <v-icon
                  color="red"
                  title="Excluir o lan√ßamento"
                  @click="openDialogDelete(item)"
                >mdi-delete</v-icon>
              </td>
            </template>
          </v-data-table>
        </v-card>
      </v-flex>
      <template
        v-if="dialogNew">
        <v-layout
          row
          justify-center>
          <v-dialog
            v-if="!loading"
            v-model="dialogNew"
            max-width="390">
            <v-card>
              <v-form
                novalidate
                enctype="multipart/form-data"
                @submit.prevent="validateBeforeSubmit">
                <v-card-title class="headline">Incluir Lan√ßamento</v-card-title>

                <v-card-text>
                  <v-layout
                    row
                    wrap>
                    <v-flex md12>
                      <v-text-field
                        v-model="newItem.nomeLoja"
                        label="Loja"
                        readonly
                      />
                    </v-flex>
                    <v-flex md12>
                      <v-autocomplete
                        v-validate="'required'"
                        v-model="newItem.tipoId"
                        :items="tipos"
                        :error-messages="errors.collect('Tipos')"
                        data-vv-name="Tipos"
                        item-value="id"
                        item-text="descricao"
                        label="Tipo"
                      />
                    </v-flex>
                    <v-flex md12>
                      <v-text-field
                        v-validate="'required'"
                        v-model="newItem.data"
                        :error-messages="errors.collect('Data')"
                        data-vv-name="Data"
                        type="date"
                        label="Data"
                      />
                    </v-flex>
                    <v-flex md12>
                      <v-text-field
                        v-validate="'required'"
                        v-formata-moeda="newItem.valor"
                        v-money="money"
                        v-model.lazy="newItem.valor"
                        :error-messages="errors.collect('Valor')"
                        data-vv-name="Valor"
                        type="tel"
                        prefix="R$"
                        label="Valor"
                      />
                    </v-flex>
                    <v-flex md12>
                      <v-textarea
                        v-model.lazy="newItem.historico"
                        label="Hist√≥rico (opcional)"
                      />
                    </v-flex>
                    <v-flex
                      class="text-md-right"
                      md12
                    >
                      <label
                        for="anexar"
                        style="cursor: pointer"
                        class="caption"
                        title="Anexar Comprovante">
                        {{ formData ? formData.get('name') : 'Anexar Comprovante' }}
                        <v-icon
                          color="dark"
                          for="anexar"
                        >mdi-paperclip</v-icon>
                        <input
                          id="anexar"
                          style="display: none"
                          type="file"
                          @change="anexar($event)"
                        >
                      </label>
                    </v-flex>
                  </v-layout>
                </v-card-text>

                <v-card-actions>
                  <v-btn
                    outline
                    style="color: black !important"
                    @click="closeDialogNew">Cancelar</v-btn>

                  <v-spacer/>
                  <v-btn
                    color="primary"
                    type="submit"
                  >Salvar</v-btn>
                </v-card-actions>
              </v-form>
            </v-card>
          </v-dialog>
          <core-progress-modal
            :show="loading" />
        </v-layout>
      </template>
      <template>
        <v-layout
          row
          justify-center>
          <v-dialog
            v-model="dialogDelete"
            max-width="390">
            <v-card>
              <v-card-title class="headline">Excluir Lan√ßamento</v-card-title>

              <v-card-text>Confirma a exclus√£o do lan√ßamento, essa opera√ß√£o n√£o poder√° ser desfeita?</v-card-text>

              <v-card-actions>
                <v-btn
                  outline
                  style="color: black !important"
                  @click="closeDialogDelete">Cancelar</v-btn>

                <v-spacer/>

                <v-btn
                  color="red"
                  @click="deleteItem">Excluir</v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-layout>
      </template>
    </v-layout>
  </v-container>
</template>

<script>
import { MONEY } from '../../constants'
import { LancamentoExtraBusiness, TiposBusiness } from '../../business'
export default {
  metaInfo: {
    titleTemplate: '%s - Lan√ßamento Extra'
  },
  data () {
    return {
      money: MONEY,
      dialogNew: false,
      dialogDelete: false,
      newItem: {
        nomeLoja: null,
        lojaId: null,
        valor: 0,
        file: null,
        tipoId: null
      },
      item: null,
      selected: [],
      filtros: {
        redeId: null,
        empresaId: null,
        lojaId: null
      },
      tipos: [],
      search: '',
      headers: [
        {
          sortable: false,
          text: 'Loja',
          value: 'nomeLoja'
        },
        {
          sortable: false,
          text: 'Tipo',
          value: 'tipo'
        },
        {
          sortable: false,
          text: 'Data',
          value: 'data'
        },
        {
          sortable: false,
          text: 'Valor',
          value: 'valor'
        },
        {
          sortable: false,
          text: 'Hist√≥rico',
          value: 'historico'
        },
        {
          sortable: false,
          text: ''
        }
      ],
      items: [],
      totalItems: null,
      loading: false,
      file: {},
      formData: null
    }
  },
  beforeMount () {
    TiposBusiness.tiposLancamentoRepasse()
      .then(response => {
        this.tipos = response.data
      })
  },
  mounted () {
    this.reload()
  },
  methods: {
    validateBeforeSubmit () {
      this.$validator.validate().then(result => {
        if (result) {
          this.loading = true
          LancamentoExtraBusiness.salvar(this.newItem)
            .then(response => {
              let msg = 'Cadastro realizado com sucesso'
              this.$root.showSucesso(msg)
              this.loading = false
              this.closeDialogNew()
              if (this.formData) {
                LancamentoExtraBusiness.uploadDocumento(response.data.id, this.formData)
                  .catch(() => this.$root.showErro('O Repasse foi feito, mas deu erro ao enviar o comprovante de anexo, entrar em contato com o suporte'))
                  .finally(() => {
                    this.pesquisar()
                  })
              } else {
                this.pesquisar()
              }
            })
            .catch(erro => {
              this.$root.showErro(erro.data)
              this.loading = false
            })
        } else {
          this.$root.showAlerta(this.$vuetify.t('$vuetify.Erro.camposObrigatorios'))
        }
      })
    },
    anexar (event) {
      if (event && event.target && event.target.files.length > 0) {
        this.file = event.target.files[0]
        this.formData = new FormData()
        this.formData.append('file', this.file)
        this.formData.append('name', this.file.name)
      }
    },
    downloadAnexo (item) {
      LancamentoExtraBusiness.downloadAnexo(item.id)
    },
    reload () {
    },
    onChangeRede (rede) {
      this.clear()
    },
    onChangeEmpresa (empresa) {
      this.clear()
    },
    lojaSelecionada (loja) {
      this.filtros.lojaId = loja.id
      this.newItem.lojaId = loja.id
      this.newItem.nomeLoja = loja.descricao
      this.pesquisar()
    },
    clear () {
      this.items = []
      this.filtros.lojaId = null
    },
    openDialogNew () {
      this.dialogNew = true
    },
    closeDialogNew () {
      this.dialogNew = false
      this.newItem.valor = 0
      this.newItem.file = null
      this.newItem.historico = null
      this.newItem.data = null
    },
    pesquisar () {
      this.loading = true
      LancamentoExtraBusiness.findAll(this.filtros.lojaId)
        .then(response => { this.items = response.data })
        .catch(error => this.$root.showErro(error.data))
        .finally(() => { this.loading = false })
    },
    openDialogDelete (item) {
      this.item = item
      this.dialogDelete = true
    },
    closeDialogDelete () {
      this.item = null
      this.dialogDelete = false
    },
    deleteItem () {
      this.loading = true
      LancamentoExtraBusiness.delete(this.item.id)
        .then(response => {
          this.$root.showSucesso(response.data)
          this.pesquisar()
          this.closeDialogDelete()
        })
        .catch(error => {
          this.$root.showErro(error.data)
        })
        .finally(() => {
          this.loading = false
        })
    }
  }
}
</script>
